import { GALLERY } from './actions.constants'
import { firestore } from '../../services/firebase';
import { showLoading, hideLoading } from 'react-redux-loading-bar'
import { toast } from 'react-toastify';

export const fetchImages = () => {
    return async dispatch => {
        dispatch({ type: GALLERY.IMAGES_REQUEST });
        dispatch(showLoading());
        try {
            await firestore.collection('images')
                .onSnapshot(snapshot => {
                    let fetchedData = {
                        images: []
                    }

                    snapshot.forEach(doc => {
                        fetchedData.images.push({
                            id: doc.id,
                            ...doc.data(),
                        });
                    });
                    
                    dispatch(imagesRequestSuccess(fetchedData))
                    dispatch(hideLoading());
                });
        } catch (error) {
            toast.error(error);
        }
        
            
    }
};

export const imagesRequestSuccess = (data) => {
    return { type: GALLERY.IMAGES_SUCCESS, ...data }
}

export const orderByNewest = () => {
    return { type: GALLERY.ORDERBY_NEWEST }
};

export const orderByOldest = () => {
    return { type: GALLERY.ORDERBY_OLDEST }
};