import React from 'react'
import { IMAGE } from './actions.constants'
import { closeModal } from './modal.actions'
import { firestore, storage } from '../../services/firebase';
import { toast } from "react-toastify";
import jimp from 'jimp';
import uuidv4 from 'uuid/v4';


export const ToastCloseButton = ({ closeToast, confirmAction }) => {
    return (
        <button style={{width: '48px', height: '48px', border: 'none', borderRadius: '0', background: "#b71d1d", color: "white", fontWeight: 'bold'}} onClick={confirmAction} autoFocus>YES</button>
    )
}

export const viewImage = (imageRef) => {
    return { type: IMAGE.VIEW_IMAGE, imageRef}
}

export const closeView = () => dispatch => {
    dispatch(closeModal());
    return { type: IMAGE.CLOSE_VIEW }
}

