export const rotateCounterClockwise = (imageRef) => {
    return { type: IMAGE.ROTATE_COUNTER_CLOCKWISE, imageRef}
}

export const deleteImage = (imageRef) => async dispatch => {
    toast.error("Remove photo from album?", {
        closeButton: <ToastCloseButton confirmAction={confirmDelete}/>
    });
    function confirmDelete() {
        (async () => {
            dispatch(closeView());
            await storage.ref().child(`images/${imageRef.name}`).delete();
            await firestore.collection('images').doc(imageRef.id).delete();
        })();
    } 
    
    return { type: IMAGE.IMAGE_DELETED }
}